import {Component, Host, h, Prop, Watch, ComponentWillLoad} from '@stencil/core';
import {isDefined, isNotEmpty} from '../../utils/utils';
import {ProgressHelper, ProgressRange} from '../../services/ProgressHelper';
import {StatusHelper} from '../../services/StatusHelper';

@Component({
  tag: 'soul-progress-bar',
  styleUrl: 'soul-progress-bar.scss',
  shadow: true,
})
export class SoulProgressBar implements ComponentWillLoad {

  @Prop({reflect: true}) label: string;
  @Prop({reflect: true}) progress: number;
  @Prop({reflect: true}) min = 0;
  @Prop({reflect: true}) max = 100;
  @Prop({reflect: true}) hideLabel: boolean = false;
  @Prop({reflect: true}) indeterminate: boolean = false;
  @Prop({reflect: true}) status: 'warning' | 'positive' | 'critical';
  @Prop({reflect: true}) color: 'neutral' | 'primary' = 'neutral';

  private supportedStatuses = ['positive', 'warning', 'critical'];

  private progressHelper = new ProgressHelper();
  private statusHelper = new StatusHelper();
  private range: ProgressRange = {min: 0, max: 100};

  componentWillLoad(): Promise<void> | void {
    this.range = this.progressHelper.getRange(this.min, this.max);
  }

  @Watch('min')
  @Watch('max')
  rangeValueChange(): void {
    this.range = this.progressHelper.getRange(this.min, this.max);
  }

  @Watch('status')
  public statusChange(newValue: string): void {
    this.statusHelper.validateStatus(this.supportedStatuses, newValue);
  }

  render() {
    const progressPercentage = this.progressHelper.getProgressPercentage(this.range, this.progress);
    const label = this.progressHelper.getLabel(this.label, this.indeterminate, progressPercentage);
    return (
      <Host role="progressbar" aria-valuemin={this.range.min} aria-valuemax={this.range.max} aria-valuenow={this.progress} aria-label={label} title={this.hideLabel ? label : ''}>
        <div class={{
          'soul-progress-bar soul-progress-bar--default': true,
          [this.indeterminate ? 'soul-progress-bar--indeterminate' : 'soul-progress-bar--determinate']: true,
          'soul-progress-bar--critical': this.status === 'critical',
          'soul-progress-bar--warning': this.status === 'warning',
          'soul-progress-bar--positive': this.status === 'positive'
        }}>
          <div class="soul-progress-bar__track">
            <div class="soul-progress-bar__fill" style={{width: `${progressPercentage}%`}}></div>
          </div>
          {this.hasLabel(label) ?
            <div class="soul-progress-bar__content">
              {isDefined(this.status) && this.statusHelper.isValidStatus(this.supportedStatuses, this.status) ?
                <div class="soul-sign  soul-sign--regular soul-sign--inline">
                  <span class="soul-sign__icon"><soul-indicator size="s" type={this.status}></soul-indicator></span>
                  <span class="soul-sign__text">
                    <span class={{
                      'soul-label soul-label--reduced h-truncate': true,
                      'soul-label--negative': this.status === 'critical',
                      'soul-label--warning': this.status === 'warning',
                      'soul-label--positive': this.status === 'positive'
                    }}>{label}</span>
                  </span>
                </div> :
                <span class="soul-label soul-label--reduced h-truncate">{label}</span>}
            </div> : ''
          }
        </div>
      </Host>
    );
  }

  private hasLabel(label: string): boolean {
    return !this.hideLabel && isNotEmpty(label);
  }

}
