@use 'modules/core';
@use 'modules/essentials/helpers';
@use 'modules/components/label';
@use 'modules/components/sign';
@use 'modules/components/progress-bar';

:host {
  display: block;
  width: 100%;
}

:host,
:host([color='neutral']) {
  --soul-progress-bar-fill-color: #{core.$soul-color-background-interactive-default-selected};
}

:host([color='primary']) {
  --soul-progress-bar-fill-color: #{core.$soul-color-background-status-brand-primary};
}

.soul-progress-bar__content {
  overflow: hidden;
}

.soul-progress-bar--warning {
  --soul-progress-bar-fill-color: #{core.$soul-color-background-status-warning};
}

.soul-progress-bar--critical {
  --soul-progress-bar-fill-color: #{core.$soul-color-background-status-critical};
}

.soul-progress-bar--positive {
  --soul-progress-bar-fill-color: #{core.$soul-color-background-status-positive};
}
